.Landing {
	text-align: left;
	color: #051e34;
}

.body {
	padding: 100px 100px 0px 100px;
	display: flex;
}

.greeting {
	text-align: left;
	font-weight: 800;
	font-family: 'Poppins', sans-serif;
	font-size: 35px;
}

.bio {
	font-weight: 500;
	font-family: 'Poppins', sans-serif;
	max-width: 50%;
}

.hyperlink {
	text-align: left;
	font-weight: 700;
	font-family: 'Poppins', sans-serif;
	background: white;
	padding: 7px 12px 7px 12px;
	border: none;
	border-radius: 9px;
	font-size: 17px;
	transition-duration: 0.3s;
	color: rgb(255, 72, 146);
	border: 3px solid rgb(255, 72, 146);
}

.hyperlink:hover {
	background: #051e34;
	color: white;
	transition-duration: 0.3s;
	border-color: #051e34;
}

nav {
	display: flex;
	justify-content: right;
}

.nav-button {
	color: white;
	background-color: #051e34;
	border-color: #051e34;
}

.nav-button:hover {
	color: #051e34;
	background-color: white;
	border-color: rgb(255, 72, 146);
	color: rgb(255, 72, 146);
}

.sign-my-guestbook {
	margin-top: 10px;
}

.pink-text {
	color: rgb(255, 72, 146);
	background: linear-gradient(to bottom, rgb(255, 72, 146), rgb(255, 91, 91));
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
}

.float-left {
	flex: 1;
}

.float-right {
	width: 50%;
}