:required {
  all: initial;
  * {
    all: unset;
  }
}

.Livestream {
	text-align: left;
	color: white;
	padding: 50px 100px 0px 100px;
}

.commentsection {
	background-color: #051e34;
	padding: 15px 50px 35px 50px;
	border-radius: 25px;
	max-width: 100%;
}

.input {
	background-color: #1e3549;
	color: white;
	font-weight: 700;
	font-family: 'Poppins', sans-serif;
	padding: 7px 12px 7px 12px;
	border: none;
	border-radius: 7px;
	font-size: 17px;
	transition-duration: 0.3s;
	min-width: 0px;
	max-width: 100%;
	display: inline;
}

/*.input:focus {
	background-color: #374b5d;
	transition-duration: 0.3s;
}*/

::placeholder {
	color: #546675;
}

.comment-form {
	display: flex;
	justify-content: space-between;
}

.your-name {
	width: 100%;
}

.leave-a-comment {
	margin-right: 10px;
	margin-left: 10px;
	width: 100%;
}

.submit {
	text-align: left;
	font-weight: 700;
	font-family: 'Poppins', sans-serif;
	background: rgb(255, 72, 146);
	padding: 7px 12px 7px 12px;
	border: none;
	border-radius: 7px;
	font-size: 17px;
	color: white;
	transition-duration: 0.3s;
}

.submit:hover {
	background: white;
	color: rgb(255, 72, 146);
	transition-duration: 0.3s;
}

h1 {
	font-weight: 800;
	font-family: 'Poppins', sans-serif;
	font-size: 35px;
}

ul {
    padding: 0;
    list-style-type: none;
}

li {
	font-family: 'Poppins', sans-serif;
	padding: 5px;
	font-size: 17px;
}

.video-container {
    position: relative;
    padding-bottom: 56.25%; /* 16:9 */
    height: 0;
}
.video-container iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 25px;
}