:required {
  all: initial;
  * {
    all: unset;
  }
}

.Guestbook {
	text-align: left;
	color: white;
	max-width: 100%;
}

.body {
	padding: 50px 100px 0px 100px;
	display: flex;
	flex-flow: row wrap;
	align-items: flex-start;
	justify-content: center;
}

.comment-section {
	background-color: #051e34;
	padding: 15px 50px 35px 50px;
	border-radius: 25px;
	max-width: 100%;
	margin: 0px;
	display: none;
}

.input {
	background-color: #1e3549;
	color: white;
	font-weight: 700;
	font-family: 'Poppins', sans-serif;
	padding: 7px 12px 7px 12px;
	border: none;
	border-radius: 7px;
	font-size: 17px;
	transition-duration: 0.3s;
	min-width: 0px;
	max-width: 100%;
	display: inline;
}

/*.input:focus {
	background-color: #374b5d;
	transition-duration: 0.3s;
}*/

::placeholder {
	color: #546675;
}

.comment-form {
	display: flex;
	justify-content: space-between;
}

.your-name {
	width: 100%;
}

.leave-a-comment {
	margin-right: 10px;
	margin-left: 10px;
	width: 100%;
}

.submit {
	text-align: left;
	font-weight: 700;
	font-family: 'Poppins', sans-serif;
	background: rgb(255, 72, 146);
	padding: 7px 12px 7px 12px;
	border: none;
	border-radius: 7px;
	font-size: 17px;
	color: white;
	transition-duration: 0.3s;
}

.submit:hover {
	background: white;
	color: rgb(255, 72, 146);
	transition-duration: 0.3s;
}

h1 {
	font-weight: 800;
	font-family: 'Poppins', sans-serif;
	font-size: 35px;
}

ul {
    padding: 0;
    list-style-type: none;
}

li {
	font-family: 'Poppins', sans-serif;
	padding: 5px;
	font-size: 17px;
}

.page {
	flex: 30%;
	flex: flex-grow;
	width: 25%;
	padding: 30px;
}

@media screen and (max-width: 992px) {
  .page {
    flex: 50%;
  }
}

img {
	width: 100%;
	border-radius: 20px;
	margin: 0px;
	padding: 0px; 
}

.page:hover .comment-section {
	display: block;
	transition-duration: 0.3s;
}

.page:hover img {
	display: none;
	transition-duration: 0.3s;
}

.nav-links {
	padding-left: 130px;
}